@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/LeMurmure-Regular.woff2);
}
@font-face {
  font-family: "font-2";
  src: url(../assets/fonts/LeMurmure-Regular.woff2);
}

// $font-1: "font-1", sans-serif;
$font-1: "font-1", serif;
$font-2: "font-2", serif;

$color-1: #fff;
$color-2: #00c1ec;
$color-3: #e7f7fe;
$color-4: rgb(141, 141, 141);
$black: rgb(40, 40, 40);
// $red: rgb(253, 87, 87);
$red: #FF0000;
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  outline: none;
  border: none;
  text-decoration: none;

  &::selection {
    color: $color-1;
    background: $color-2;
  }
}
html { 
  scroll-behavior: unset;
}
a,
a:visited {
  color: $color-4;
}
body {
  font-family: $font-1;
  height: 100vh;
  background-color: $color-1;
  color: $black;
  overflow: hidden;

  @media screen and (max-width: 430px){
    position: relative;
    max-width: 100vw;
    overflow-y: initial;
  }
}
main {
  position: relative;
}

.input {
  font-family: $font-1;
}
 