.dynamic-text {
  position: relative;
  display: flex;

  .simply {
    // color: $color-4;
    color: $black;  }



  #text-target {
    // border: red 1px solid;
    display: flex;
    width: 300px;
    text-align: left;
    animation: color 30s alternate infinite;
    perspective: 200px;
    // transform: translateX(26px);
    transform: translateX(36px);

  }
  .word {
    position: absolute;
    // box-sizing: border-box;
  }
  @keyframes color {
    0% {
      // color: $color-2;
      color: $black; 
    }
    50% {
      // color: $black; 
      color: $red; 
    }
    100% {
      // color: $color-2;
      color: $black; 
      // color: $red; 
  
    }
  }
  .letter {
 
    transform: translateZ(200px);
  }
  @keyframes anim {
    25% {
      transform: translateZ(0px);
      opacity: 1;
    }
    32% {
      opacity: 0;
      transform: translateZ(-280px) translateX(200px);
    }
  }
}
