.logo  {
  top: 15px;
  left: 5%;
  transform: translateX(-50%);
  transform: scale(0);
  animation: logoProject 2s ease forwards;

  @keyframes logoProject {
    to {
      transform: scale(1);
    }
  }
}

.home {
  height: 100vh;
  width: 100vw;
  position: absolute;

 


  .home-main {
    display: grid;
    align-items: center;
    justify-items: center;
    padding-left: 1rem;
    width: auto;
    height: 95%;
    overflow: hidden;

    .main-content {
      user-select: none;

      h1 {
        //font-size: 3.7rem;
        font-size: 19rem;
        position: relative;
        font-family: $font-2;
        color: $black;

        @media screen and (max-width: 768px){
          font-size: 8rem;
        }

        @media screen and (max-width: 650px){
          font-size: 6rem;
        }
        @media screen and (max-width: 350px){
          font-size: 5rem;
        }
        @media screen and (min-width: 1200px){
          font-size: 19rem;
        }

    
      }
      h2 {
      font-size: 14rem;
      font-weight: 100;
    
        @media screen and (max-width: 768px){
          font-size: 6rem;
        }
        @media screen and (max-width: 650px){
           font-size: 4rem;
        }
        @media screen and (max-width: 350px){
          font-size: 3rem;

        }
        @media screen and (min-width: 1200px){
           font-size: 14rem;
        
      }
    }
  }
}
}
 
 