.contact {
  position: absolute;
  width: 100vw;
  height: 100vh;

  &:after {
    // content: "";
    // height: 700px;
    // width: 700px;
    // background: $red;
    // border-radius: 50%;
    // position: absolute;
    // top: 150px;
    // right: -460px;
    // z-index: -10;
    // animation: contactAnim 25s alternate infinite;

    // @media screen and (max-width: 840px){
    //   height: 400px;
    //   width: 400px;
    //   top: 520px;
    //   right: -140px;
    // }
    // @media screen and (max-width: 430px){
    //   display: none;
    // }

    @keyframes contactAnim {
      50% {
        transform: scale(1.4);
      }
      100% {
        transform: scale(1);
      }
    }
  }
  .navigation {
    @media screen and (max-width: 440px){
      font-size: 1.5rem;
    }
  }
  .logo {
    top: 15px;
    left: 5%;
    transform: translateX(-50%);
    transform: scale(0);
    animation: logoProject 2s ease forwards;
  
    @keyframes logoProject {
      to {
        transform: scale(1);
      }
    }
  }
 
 
  .contact-infos {
     position: absolute;
    // bottom: 5%;
    // left: 10%;
    // margin: 7% 0 0 30%;
    margin: 8% 22%;
    line-height: 70px;
    height: 50%;
    width: 80%;
    // max-width: 500px;
 
    @media screen and (max-width: 740px){
      display: block;
      // height: 200px;
      width: 100%;
      margin: 50% 3%;
      padding: 10px;

      // @media screen and (max-width: 740px){
      //   margin: 50% 8%;
      // }
      @media screen and (min-width: 1200px){
        width: 35%;
        height: 83%;
      }
  

      @media screen and (max-width: 360px){
       margin: 20% 4%;

       }
    }
  

    .about {
      position: relative;
    
      @media screen and (max-width: 360px){

      margin: 30% 30% 0% 0%;
    }


     .content {
       position: absolute;
      }
   }
 }

   .about p {
     font-size: 80px;
     line-height: 83px;

     @media screen and (max-width: 740px){
       font-size: 50px;
       line-height: 55px;
       // width: 200px;
       // height: 200px;

      }

      @media screen and (max-width: 500px){
        font-size: 50px;
      }
     @media screen and (max-width: 440px){
       display: block;
     }
     @media screen and (max-width: 360px){
       font-size: 30px;
       line-height: 35px;
 
     }
   
 
      // }
   }


 



    h4 {
      // h4=mail
      color: $color-4;
      font-size: 2rem;
      margin-bottom: 10px;
      @media screen and (max-height: 640px){
       display:none;

      }
    }
    
    .email {
      display: block;

     
    }
    .email .content p {
       font-size: 30px;
      transition: 0.25s;

      @media screen and (max-height: 640px){
        font-size: 20px;
      }
      &:hover {
        transform: translateX(5px);
      }
    
   

    
     
  }
}







    
