.navigation {
  z-index: 999;
  position: absolute;
  top: 4.9rem;
  left: 1.4rem;
  // font-size: 1.4rem;
  // line-height: 28px;
  font-size: 2.4rem;
  line-height: 38px;
  color: $color-4;

  &:before {

    // etoile rouge: 
    //  content: "â˜…";
    // color: $red;



    // content: '';
    // position: absolute;
    // height: 300px;
    // width: 300px;
    // border-radius: 50%;
    // background: $color-3;
    // top: -150px;
    // left: -160px;
    // z-index: -1;
    // animation: navAnim 20s infinite alternate;

    // @keyframes navAnim {
    //   50% {
    //     transform: scale(1.2);
    //   }
